import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { FormBuilder, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { LoginSessionService } from '../../shared/login-session.service';
import { Router } from '@angular/router';


@Component({
  selector: 'app-product-popup',
  templateUrl: './product-popup.component.html',
  styleUrls: ['./product-popup.component.css']
})
export class ProductPopupComponent implements OnInit {
  quantity: AbstractControl;
  myForm: FormGroup;
  errorMessage: Boolean=false;
  constructor(public dialogRef: MatDialogRef<ProductPopupComponent>, @Inject(MAT_DIALOG_DATA) public data: any, fb: FormBuilder, private loginSessionService: LoginSessionService, private router: Router) 
  { 
    this.myForm=fb.group({
      quantity:[null]
    });
    // quantity:[null,Validators.compose([Validators.required, Validators.pattern('^\d+$')])]
    this.quantity=this.myForm.controls['quantity'];
 

  }

  onSubmit(formValue, formValid)
  {
    if(formValid == false)
    {
      this.errorMessage=true;
      return;
    }

    let jsonDetails={ id:this.data.id, bookingType:2, price: this.data.price, quantity: formValue.quantity };
    localStorage.setItem('bookingDetails',JSON.stringify(jsonDetails));

    // set product token in bookingToken
    this.loginSessionService.checkLogin().subscribe(        
        (res) => {
            // if(localStorage.getItem('bookingToken') && localStorage.getItem('bookingToken') != 'productToken')
            // {
            //     localStorage.setItem('bookingToken','productToken');
            // }
            // else
            // {
            //     if(!localStorage.getItem('bookingToken'))
            //     {
            //         localStorage.setItem('bookingToken','productToken');
            //     }
            // }
            if(res.loginSessionValue == true)
            {                
                this.router.navigateByUrl('profileInfo');
            }
            else
            {                
                this.router.navigateByUrl('login');
            }
        });
  

        this.dialogRef.close();










  }
  ngOnInit() {
  }

}



