import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators, AbstractControl, FormControl } from '@angular/forms';
import { ProfileModel } from './models/edit-info.model';
import { LoginSessionService } from '../../shared/login-session.service';
import { EditProfileServiceService } from './services/edit-profile-service.service';
import { ProfileInfoModel } from './models/profileInfoModel.model';
import { MatSnackBar } from '@angular/material';

//import { validatePassword } from './services/validate-confirm-password';

@Component({
  selector: 'app-edit-profile',
  templateUrl: './edit-profile.component.html',
  styleUrls: ['./edit-profile.component.css'],
  providers: [EditProfileServiceService]
})
export class EditProfileComponent implements OnInit {    
    myForm: FormGroup;
    userInfo :ProfileModel;
    profileInfo: ProfileInfoModel;
    userId: any;
    updatedValues: any;
    status: any;
    
  constructor(fb: FormBuilder, private editProfileService: EditProfileServiceService, private snackBar: MatSnackBar) {
      this.myForm = fb.group({
            firstName: [null, Validators.compose([Validators.required])],
            lastName: [null, Validators.compose([Validators.required])],
            phoneNumber: [null, Validators.compose([Validators.required])],
            email: [null, Validators.compose([Validators.required])],
            //password: [null, Validators.compose([Validators.required])],
            newPassword: [null],
            confirmNewPassword: [null],
            state: [null, Validators.compose([Validators.required])],
            city: [null, Validators.compose([Validators.required])]
        }, {validator: this.validatePassword});
        
        
        editProfileService.editProfileMethod().subscribe((res)=>{
          console.log('profile info');
         
          this.profileInfo=res;
          this.myForm.patchValue({city:this.profileInfo.city,email:this.profileInfo.email,firstName:this.profileInfo.firstName,lastName:this.profileInfo.lastName,phoneNumber:this.profileInfo.phoneNumber,state:this.profileInfo.state,password: this.profileInfo.password});
          this.userId=this.profileInfo._id;          
        }); 
        
        

   }

  onSubmit(formValue, formValid)
  {
    // let updatedValues;
    if(formValue == false)
    {
      console.log('not working');
    }
    else
    {
      // this.updatedValues['userId']=this.userId;
      // this.updatedValues.push({'userId':this.userId});
      
      
      
      this.updatedValues={'userId':this.userId, 'firstName':formValue.firstName,'lastName':formValue.lastName,'phoneNumber':formValue.phoneNumber,'state':formValue.state, 'email':formValue.email, 'city':formValue.city, 'newPassword': formValue.newPassword};

      this.editProfileService.updateProfileMethod(this.updatedValues).subscribe((res)=>{
        if(res.result == 0)
        {
          this.status="Updation Failure Error.";
        }
        else if(res.result == 1)
        {
          this.status="User Information Updated Successfully.";
        }
        else
        {
          if(res.result == 2)
          {
            this.status="No Information Updated.";
          }
        }
        this.snackBar.open(this.status, 'ok', {
          duration:2000
        });
        
      });
      // console.log(this.updatedValues);
      
      
      /*  updatedValues['firstName']=formValue.firstName;
      updatedValues['lastName']=formValue.lastName;
      updatedValues['phoneNumber']=formValue.phoneNumber;
      updatedValues['state']=formValue.state;
      // updatedValues['password']=formValue.password;
      updatedValues['email']=formValue.email;
      updatedValues['city']=formValue.city;
      console.log(updatedValues); */
    }
  }

  validatePassword(control: AbstractControl)
  {
    if(control.get("newPassword").value !== null &&  control.get("newPassword").value !== control.get("confirmNewPassword").value)
    {      
      return {invalidValue: true};
    }
      return null;    
    
  }

  ngOnInit() {
  }

}



