import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from "@angular/router";
import { Observable } from 'rxjs/Rx';
import "rxjs/add/observable/forkJoin";

@Injectable()
export class AppointmentBookingService implements Resolve<any> {

  constructor(private http: Http) { }
  resolve(activatedRouteSnapshot:ActivatedRouteSnapshot, routerStateSnapshot: RouterStateSnapshot)
  {
    return this.http.get("/api/frontend/appointmentBookingListApi").map(
      data => {
        return data.json();
      }
    );
  }
  getAppointmentTimeSlotsMethod(selectedDate: any)
  {
    var jsonData={bookingDate : selectedDate};
    return this.http.post("/api/frontend/getAppointmentTimeSlotsApi",jsonData).map(
      data => {
         return data.json();
      }
    );
  }


}



