import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { RegisterService } from './services/register.service';
import { LoginSessionService } from '../../shared/login-session.service';
import { Router } from '@angular/router';
import { RegisterModel, LoginCheckModel } from './models/registerModel.model';
import { MatSnackBar } from "@angular/material";
import { LoginService } from "../login/services/login-service.service";

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css'],
  providers: [RegisterService, LoginService]
})
export class RegisterComponent implements OnInit {
    myForm: FormGroup;
    userInfo :RegisterModel;
    loginCheck : LoginCheckModel;
    constructor(fb: FormBuilder, private registerService: RegisterService, private loginSessionService: LoginSessionService, private router: Router, private matSnackBar: MatSnackBar, private loginService : LoginService) {
        this.myForm = fb.group({
            firstName: [null, Validators.compose([Validators.required])],
            lastName: [null, Validators.compose([Validators.required])],
            password: [null, Validators.compose([Validators.required])],
            email: [null, Validators.compose([Validators.required])]          
        });
    }
    onSubmit(form: any)
    {
        if (form.valid) {
            this.registerService.saveUserInfo(form.value.firstName, form.value.lastName, form.value.password, form.value.email, form.value.phoneNumber)
                .subscribe(
                userInfo => {
                    this.userInfo = userInfo;
                    
                    if (this.userInfo.userCreationStatus == 1)
                    {
                        // this.loginService.login()
                        console.log('this.userInfo');
                        console.log(this.userInfo);

                        this.loginService.login(form.value.email, form.value.password).subscribe(
                            (result) => {
                                this.loginCheck = result;
                                if(this.loginCheck.status == 1) {
                                    let name=form.value.firstName+' '+form.value.lastName;
                                    var loginResult={loginResult:true,name:name};
                                    this.loginSessionService.sendMessage(loginResult);                        
                                    
                                    if (localStorage.getItem('bookingDetails'))
                                    {
                                        this.matSnackBar.open('Congrats! You are successfully login.','Ok',{duration:2000});
                                        this.router.navigateByUrl('profileInfo');
                                        return;
                                    }
                                    this.router.navigateByUrl('booking');
                                }
                                else
                                {
                                    if(this.loginCheck.status == 0) {
                                        this.matSnackBar.open('Login failure error.','Ok',{duration:2000});
                                    }
                                }
                                
                        });
                        
                    }
                    else
                    {
                        if(this.userInfo.emailAlreadyExist == 1)
                        {
                            this.matSnackBar.open('Email already exists.','Ok',{duration:2000});
                            return;
                        }
                        else
                        {
                            this.matSnackBar.open('Registration failure error.','Ok',{duration:2000});
                            return;
                        }
                    }

                    
                },
                error => console.log(error),
                () => console.log('finished') 
                );

            // );
          //  console.log(form.value.firstName);
        }
        else
        {
            console.log('correct');
        }
    }
    redirectLoginPage()
    {
        this.router.navigateByUrl('login');
    }
    ngOnInit() {
        // console.log(this.userInfo.length);
    }

}



