import { Component, OnInit } from '@angular/core';
import { routerTransition } from '../../router.animations';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { LoginService } from './services/login-service.service';
import { LoginModel } from './login.model';
import { LoginSessionService } from '../../shared/login-session.service';
import { Router } from '@angular/router';
import { MatSnackBar } from "@angular/material";


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
  animations: [routerTransition()],
  host: { '[@routerTransition]': '' },
  providers: [LoginService]
})
export class LoginComponent implements OnInit {
    
    
    // loginStatus: boolean = true;
    loginModel : LoginModel;
    
    myForm: FormGroup;
    loginSuccess : boolean = true;
    constructor(fb: FormBuilder, private loginService: LoginService, private loginSessionService: LoginSessionService, private router: Router, private matSnackBar: MatSnackBar) {
        
        
        this.myForm = fb.group({
            //email: [null, Validators.compose([Validators.required, Validators.pattern("^[a-z0-9!#$%&'*+\/=?^_`{|}~.-]+@[a-z0-9]([a-z0-9-]*[a-z0-9])?(\.[a-z0-9]([a-z0-9-]*[a-z0-9])?)*$/i")])],
            email: [null, Validators.compose([Validators.required])],
            password: [null, Validators.compose([Validators.required])]
        });








    }
    onSubmit(form: any):void
    {
        if (form.valid) {       
            this.loginService.login(form.value.email, form.value.password).subscribe(
                (result) => {
                    console.log(result);
                    this.loginModel = result;
                    console.log('this.loginModel'); 
                    console.log(this.loginModel);
                    // this.loginModel.name=loginStatus.name;
                    if(this.loginModel.status == 1)
                    {
                        this.loginSuccess = true;
                        console.log('this.loginModel.message');
                        console.log(this.loginModel);
                        console.log(this.loginModel.message);

                        var loginResult={loginResult:true,name:`${this.loginModel.message.firstName} ${this.loginModel.message.lastName}`};
                        this.loginSessionService.sendMessage(loginResult);
                        // service                                   
                        // if (!localStorage.getItem('bookingDetails'))                    
                        // {
                        //     this.matSnackBar.open('Congrats! You are successfully login.','Ok',{duration:2000});
                        //     this.router.navigateByUrl('booking');
                        // }
                        if (localStorage.getItem('bookingDetails'))
                        {
                            this.matSnackBar.open('Congrats! You are successfully login.','Ok',{duration:2000});
                            this.router.navigateByUrl('profileInfo');
                             return;
                        }
                        this.router.navigateByUrl('booking');
                                         
                    }
                    else
                    {
                        this.loginSuccess = false;
                        this.matSnackBar.open('Login details are incorrect.','Ok',{duration:2000});
                    }
                    
                    console.log(this.loginModel.status);
                },
                (error) => {
                    console.log(error);
                },
                () => {
                    console.log('operation finished.');
                }
            );
        }  
        else
        {
            // alert('very bad');
            console.log('form error occurs');
            return;
        }   
        
    }

    redirectRegisterPage()
    {
        this.router.navigateByUrl('register');
    } 

    ngOnInit() {
        // this.loginModel.loginStatus = true;
    }


}



