import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, AbstractControl, Validators } from '@angular/forms';
import { LoginSessionService } from '../../shared/login-session.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-payment-without-booking',
  templateUrl: './payment-without-booking.component.html',
  styleUrls: ['./payment-without-booking.component.css']
})
export class PaymentWithoutBookingComponent implements OnInit {
  serviceOrProductName: AbstractControl;
  serviceOrProductPrice: AbstractControl;
  myForm: FormGroup;
  errorMessage: Boolean=false;
  constructor(fb: FormBuilder, private loginSessionService: LoginSessionService, private router: Router) {
    this.myForm=fb.group(
      {
        serviceOrProductName: [null,Validators.compose([Validators.required])],
        serviceOrProductPrice: [null, Validators.compose([Validators.required])]
      }
    );
    this.serviceOrProductName=this.myForm.controls['serviceOrProductName'];
    this.serviceOrProductPrice=this.myForm.controls['serviceOrProductPrice'];
   }
   onSubmit(formValue, formValid)
   {
      if(formValid == false)    
      {
        this.errorMessage=true;
        console.log('form is not valid');
        return;
      }
      let jsonDetails={ serviceOrProductName:formValue.serviceOrProductName, bookingType:3, serviceOrProductPrice: formValue.serviceOrProductPrice };
      localStorage.setItem('bookingDetails',JSON.stringify(jsonDetails));
      this.router.navigateByUrl('profileInfo');
      // this.loginSessionService.checkLogin().subscribe(        
      //   (res) => {
      //       if(localStorage.getItem('bookingToken') && localStorage.getItem('bookingToken') != 'withoutBookingToken')
      //       {
      //           localStorage.setItem('bookingToken','withoutBookingToken');
      //       }
      //       else
      //       {
      //           if(!localStorage.getItem('bookingToken'))
      //           {
      //               localStorage.setItem('bookingToken','withoutBookingToken');
      //           }
      //       }
      //       if(res.loginSessionValue == true)
      //       {                
      //           this.router.navigateByUrl('profileInfo');
      //       }
      //       else
      //       {                
      //           this.router.navigateByUrl('login');
      //       }
      //   });

   }
  ngOnInit() {
  }

}



