import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';
import { PaymentDataAppointmentBookingModel } from "../models/paymentDataAppointmentBooking.model";
import { HttpClient } from "@angular/common/http";
@Injectable()
export class BookingService {

  constructor(private http: Http, private httpClient : HttpClient) { }
  // get service categories during page load.
  // services data
  getBookedServicesDataMethod() {
      return this.http.get('/api/frontend/getBookedServicesData')
          .map(res => {            
            return res.json();
          });
  }

  getBookedProductsDataMethod() {
      return this.http.get('/api/frontend/getBookedProductsData')
          .map(res => {            
            return res.json();
          });
  }
  getWithoutBookingDataMethod()
  {
    return this.http.get('/api/frontend/getWithoutBookingData')
    .map(res => {            
      return res.json();
    });    
  }
  getBookedAppointmentsDataMethod()
  {
    return this.http.get('/api/frontend/getBookingAppointmentsData')
    .map(res => {            
      return res.json();
    });
  }
  
}



