import { Component, OnInit } from '@angular/core';
import { AppointmentBookingService } from './services/appointment-booking-service.service';
import { BookingService } from "../booking-service/services/booking-service.service";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
import { AppointmentBookingModel, AppointmentDurationModel } from "./models/appointmentBooking.model";
import { ActivatedRoute, Router } from "@angular/router";
// import * as _ from "lodash";

@Component({
  selector: 'app-appointment-booking',
  templateUrl: './appointment-booking.component.html',
  styleUrls: ['./appointment-booking.component.css'],
  providers: [AppointmentBookingService, BookingService]
})
export class AppointmentBookingComponent implements OnInit {
  myForm: FormGroup;
  minDate = new Date();
  price: number=null;
  timeSlots: any[]=[];
  timeDuration : any;
  appointmentDuration: AppointmentDurationModel[];
  bookedRecords : any[];
  constructor(private fb: FormBuilder, private appointmentBookingService: AppointmentBookingService, private route: ActivatedRoute, private router: Router, private bookingService : BookingService) { 
  }

  ngOnInit() {
    let todayDate=new Date();
    
    // for(let i=5;i<=30;i=i+5)
    // {
    //   this.durations.push(i);
    // }
    // console.log(this.durations);
    this.myForm=this.fb.group({
      companyName: ['',Validators.compose([Validators.required])],
      description: ['',Validators.compose([Validators.required])],
      // bookingDates: [todayDate],
      bookingDate: ['',Validators.compose([Validators.required])],
      id: ['',Validators.compose([Validators.required])],
      timeSlot: ['',Validators.compose([Validators.required])],
      bookingType : [4]
    });
    this.route.data.subscribe((data)=>{
      this.appointmentDuration=data.appointmentData;
      console.log('this.appointmentDuration');
      console.log(this.appointmentDuration);
    });
  }

  // this function hits when user select any date from datepicker
  getTimeSlots(dateInfo)
  {
    this.timeSlots = [];
    let selectedDate=dateInfo.getDate()+"/"+(parseInt(dateInfo.getMonth()+1))+"/"+dateInfo.getFullYear();
    let startTimeSlot: number=600; // start time in minutes
    let startTimeSlotTemp=startTimeSlot;
    console.log(dateInfo);
    let duration=this.timeDuration;
    let endTimeSlot;
    let timeSlotsInMinutes: any[]=[];
    let restTimeDuration=10;  // duration for take rest
    let tempTimeSlots: any[]=[];
    let dbTimeSlots: any[]=[];
    let timeSlotDuration=duration+restTimeDuration; //
    let bookedServiceRecords : AppointmentBookingModel;
    let bookedAppointmentRecords : AppointmentBookingModel;

    console.log(selectedDate); 
    
    for(startTimeSlot;startTimeSlot<=1320;startTimeSlot=startTimeSlot+duration+restTimeDuration)
    {
      endTimeSlot=startTimeSlot+duration+restTimeDuration;
      
      // if some duration of lunch time is more than 885, then we have reset loop of startTimeSlot variable from 885.
      if(endTimeSlot > 885)
      {
        if(endTimeSlot-885 < duration+restTimeDuration)
        {
          startTimeSlot=885;
          endTimeSlot=startTimeSlot+duration+restTimeDuration;
        }  
      }

      if((startTimeSlot < 840 && endTimeSlot <= 840) || (startTimeSlot >= 885 && endTimeSlot >= 885 && endTimeSlot <= 1320))
      { 
        // timeSlotsInMinutes.push({startTimeSlot:startTimeSlot,endTimeSlot:endTimeSlot-10});
        timeSlotsInMinutes.push({startTimeSlot:startTimeSlot,endTimeSlot:endTimeSlot,isDisabled:false});
      }
    }
    

    // dbTimeSlots=[{"timeSlot":"10:00","duration":5},{"timeSlot":"13:30","duration":30}];
    this.appointmentBookingService.getAppointmentTimeSlotsMethod(selectedDate).subscribe(appointmentBookingResponse => {
      this.bookingService.getTimeSlotsForDateMethod(selectedDate).subscribe(bookingServiceResponse => {
        let dbTimeSlots;
        bookedServiceRecords = bookingServiceResponse;
        bookedAppointmentRecords = appointmentBookingResponse;
        console.log('2 arrays responses');
        console.log(appointmentBookingResponse);
        console.log(bookingServiceResponse);
        if(bookedServiceRecords.status == true || bookedAppointmentRecords.status == true)
        {
          if(bookedServiceRecords.status == true && bookedAppointmentRecords.status == true)
          {
            this.bookedRecords = appointmentBookingResponse.data.concat(bookingServiceResponse.data);
          }
          else
          {
            this.bookedRecords = appointmentBookingResponse.status == true ? appointmentBookingResponse.data : bookingServiceResponse.data;
          }
          
          let tempDbTimeSlots: any[]=[];
          
          this.bookedRecords.forEach((singleTimeSlot,singleTimeSlotIndex) => {
            let hoursMinutesArr=singleTimeSlot.timeSlot.split(":");
            let hoursMinutes : number = parseInt(hoursMinutesArr[0])*60+parseInt(hoursMinutesArr[1]);
            
            tempDbTimeSlots.push({"dbStartTimeSlot":hoursMinutes,"dbEndTimeSlot": hoursMinutes +  singleTimeSlot.duration,"duration" : singleTimeSlot.duration });
          });

          timeSlotsInMinutes.forEach((singleTimeSlot,singleTimeSlotIndex)=>{
            tempDbTimeSlots.forEach((tempDbSingleTimeSlot,tempSingleTimeSlotIndex)=> {
              
              
              
              if((tempDbSingleTimeSlot.dbStartTimeSlot >= singleTimeSlot.startTimeSlot && tempDbSingleTimeSlot.dbEndTimeSlot <= singleTimeSlot.endTimeSlot) || (tempDbSingleTimeSlot.dbStartTimeSlot <= singleTimeSlot.startTimeSlot && tempDbSingleTimeSlot.dbEndTimeSlot >= singleTimeSlot.endTimeSlot) || (tempDbSingleTimeSlot.dbStartTimeSlot < singleTimeSlot.endTimeSlot && tempDbSingleTimeSlot.dbEndTimeSlot >= singleTimeSlot.endTimeSlot) || (tempDbSingleTimeSlot.dbStartTimeSlot >= singleTimeSlot.startTimeSlot && tempDbSingleTimeSlot.dbEndTimeSlot < singleTimeSlot.startTimeSlot))
              {
                console.log(`duration ${tempDbSingleTimeSlot.duration}`); 
                
                timeSlotsInMinutes[singleTimeSlotIndex]={startTimeSlot:singleTimeSlot.startTimeSlot,endTimeSlot:singleTimeSlot.endTimeSlot,isDisabled:true}; 

                console.log('timeslots2');
                console.log(`${singleTimeSlot.startTimeSlot} ${singleTimeSlot.endTimeSlot}`);
                console.log('db timeslots2');
                console.log(tempDbSingleTimeSlot.dbStartTimeSlot);
              }
            });
            this.getTimeSlot(singleTimeSlot, singleTimeSlotIndex, timeSlotsInMinutes[singleTimeSlotIndex].isDisabled);

          });
          console.log(this.timeSlots);
        }
        else 
        {
          timeSlotsInMinutes.forEach((singleTimeSlot,singleTimeSlotIndex)=>{
            this.getTimeSlot(singleTimeSlot, singleTimeSlotIndex, timeSlotsInMinutes[singleTimeSlotIndex].isDisabled);
          });
        }
      });
     });
    
  }

  getTimeSlot(singleTimeSlot: any, singleTimeSlotIndex: any, isDisabled: any)
  {
    let tempStartTimeInMinutes=singleTimeSlot.startTimeSlot/60;
    let tempEndTimeInMinutes=(singleTimeSlot.endTimeSlot-10)/60;
    

    let tempStartTimeInHour=typeof tempStartTimeInMinutes.toString().split('.')[1] != "undefined" ? Math.round(parseFloat((parseFloat('.'+tempStartTimeInMinutes.toString().split('.')[1])*60).toString())).toString().length == 1 ?tempStartTimeInMinutes.toString().split('.')[0]+':0'+Math.round(parseFloat((parseFloat('.'+tempStartTimeInMinutes.toString().split('.')[1])*60).toString())):tempStartTimeInMinutes.toString().split('.')[0]+':'+Math.round(parseFloat((parseFloat('.'+tempStartTimeInMinutes.toString().split('.')[1])*60).toString()))  : tempStartTimeInMinutes.toString().split('.')[0]+':00';

    let tempEndTimeInHour=typeof tempEndTimeInMinutes.toString().split('.')[1] != "undefined" ?  
    Math.round(parseFloat((parseFloat('.'+tempEndTimeInMinutes.toString().split('.')[1])*60).toString())).toString().length == 1 ?tempEndTimeInMinutes.toString().split('.')[0]+':0'+Math.round(parseFloat((parseFloat('.'+tempEndTimeInMinutes.toString().split('.')[1])*60).toString())):tempEndTimeInMinutes.toString().split('.')[0]+':'+Math.round(parseFloat((parseFloat('.'+tempEndTimeInMinutes.toString().split('.')[1])*60).toString()))  : tempEndTimeInMinutes.toString().split('.')[0]+':00';      
    

    this.timeSlots.push({startTimeSlot:tempStartTimeInHour, endTimeSlot:tempEndTimeInHour, isDisabled: isDisabled});
  }

  timeDurationSelect()
  {
    let temp;
    let duration=this.myForm.controls['id'].value;
    temp=this.appointmentDuration.filter(function(data) {
       return data._id == duration;
    });
    this.price=temp[0].price;
    this.timeDuration = temp[0].duration;
  }

  submitAppointment(formValues: any,formValid: any)
  {
    console.log(formValid);
    if(formValid == false)
    {
      console.log("Please check fields");
      return;
    }
    
    let selectedDate=formValues.bookingDate.getDate()+"/"+(parseInt(formValues.bookingDate.getMonth()+1))+"/"+formValues.bookingDate.getFullYear();
    formValues.bookingDate=selectedDate;
    console.log(formValues);
    localStorage.setItem('bookingDetails',JSON.stringify(formValues));
    // localStorage.setItem('bookingToken','appointmentToken');
    this.router.navigateByUrl('profileInfo');
  }

}



