import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';
@Injectable()
export class ProfileInfoService {

  constructor(private http: Http) { }

  getLoginInfoAndBookingInfoMethod(serviceType, bookingType)
  {
    let productId:any;
    let jsonData: any;
    if(bookingType == 1)
    {
      jsonData={serviceType:serviceType, bookingType: bookingType};
    }
    else if(bookingType == 2)
    {
        jsonData={productId:serviceType, bookingType: bookingType};
    }
    else if(bookingType == 4)
    {
        jsonData={appointmentId:serviceType, bookingType: bookingType};
    }
    else
    {
      if(bookingType == 3)
      {
          jsonData={bookingType: bookingType};
      }      
    }
    
    
    return this.http.post('/api/frontend/getLoginInfoAndBookingInfo',jsonData).map((res)=>{
      // console.log('hhhhh');
      // console.log(res.json());
      return res.json();
    });
  }

  payment(paymentId, serviceId, timeSlot, phoneNumber, state, city, bookingDate, bookingType, price, companyName ?: any, description ?: any)
  {
    let jsonData: any;
    let productId: any;
    let appointmentId : any;
    let quantity: any;
    let serviceOrProductPrice: any;
    let serviceOrProductName: any;

    if(bookingType == 1)
    {
      jsonData={paymentId, serviceId, timeSlot, phoneNumber, state, city, bookingDate, bookingType, price};
      // return this.http.post('/api/payment', jsonData).map((response)=>{
      //     console.log('payment service response: ');
      //     console.log(response);
      //     return response;
      // });
    }
    else if(bookingType == 2)
    {
      productId=serviceId;
      quantity=timeSlot;        
      jsonData={paymentId, productId, quantity, phoneNumber, state, city, bookingType, price};
      // return this.http.post('/api/payment', jsonData).map((response)=>{
      //     console.log('payment product response: ');
      //     console.log(response);
      //     return response;
      // });        
    }
    else if(bookingType == 4)
    {
      appointmentId=serviceId;
      jsonData={paymentId, appointmentId, timeSlot, phoneNumber, state, city, bookingType, price, companyName, description, bookingDate};
      // return this.http.post('/api/payment', jsonData).map((response)=>{
      //     console.log('payment product response: ');
      //     console.log(response);
      //     return response;
      // });        
    }
    else
    {
      if(bookingType == 3)
      {
        price=serviceId;
        serviceOrProductName=timeSlot;
        jsonData={paymentId, price, serviceOrProductName, phoneNumber, state, city,bookingType};
        
      }
    }
    return this.http.post('/api/frontend/payment', jsonData).map((response)=>{
            // console.log('payment service response: ');
            console.log(response.json());
            return response.json();
        });
    

  }


  

}



