import { Component, OnDestroy } from '@angular/core';
import { navigationRoutes } from '../navigationRoutes.routes';
import { Router } from '@angular/router';

import { HomeComponent } from '../navigationPages/home/home.component';
import { AboutComponent } from '../navigationPages/about/about.component';
import { ContactComponent } from '../navigationPages/contact/contact.component';

import { trigger, state, style, transition, animate } from '@angular/animations';

import { LoginSessionService } from '../shared/login-session.service';
import { Subscription } from 'rxjs/Subscription';
import { LoginService } from '../shared/login.service';


@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css'],
  providers: [LoginService]
  
})
export class HeaderComponent implements OnDestroy {

     
    state: string;
    name: string;
    logoutMessage: any;
    subscription: Subscription;
    navbarStatus: number=0;
    constructor(private loginSessionService: LoginSessionService, private loginService: LoginService, private router: Router) {

        console.log("Atleast i came here in login component");
        this.subscription = this.loginSessionService.getMessage().subscribe(m => {             
            this.logoutMessage = m.text.loginResult;
            this.name=m.text.name;
        });

        this.loginSessionService.checkLogin().subscribe((loginResponse) => {
            
            if (loginResponse.status == 1) {
                this.name=loginResponse.name;
                this.logoutMessage = true;                
            }
            else
            {
                if (loginResponse.status == 0) {
                    this.logoutMessage = false;
                }
                
            }
                });

            if(window.innerWidth > 767)
            {
                this.navbarStatus=1;    
            }
                
    }

    
    ngOnDestroy() {
        this.subscription.unsubscribe();
  }

  navClick()
  {
    if(window.innerWidth <= 767)
    {
        this.navbarStatus=0;    
    }
      
  }

  navbarClick()
  {
    if(window.innerWidth <= 767){
        
        if(this.navbarStatus == 0)
        {

            this.navbarStatus=1;
            
        }
        else
        {
            if(this.navbarStatus == 1)
            {
                this.navbarStatus=0;
            }   
        }
        
    } 
  }

  navToggle(state)
  {
      this.state = "scrollDown";
  }

  pressLogout()
  {
      this.loginService.pressLogout().subscribe((res) => {
        this.logoutMessage = false;
        localStorage.clear();
        this.router.navigateByUrl('home');
      });
  }

}



