import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class BookingService {

  constructor(private http: Http) { }

  // get service categories during page load.
  serviceCategorySelectionMethod() {
      return this.http.get('/api/frontend/serviceCategorySelection')
          .map(res => {            
            return res.json();
          });
  }



  // get type of services corresponding to serviceId.
  getTypeOfServicesMethod(serviceId, gender?: string) {
      var jsonData;
      jsonData = { serviceId:serviceId, gender: gender };
      return this.http.post('/api/frontend/getTypeOfServices', jsonData)
          .map((res) => {
              console.log(res.json());
              return res.json();
          });
  }

  saveBookingMethod()
  {
      // return this.http.post()
  }


  getTimeSlotsForDateMethod(bookingDate)
  {
    let json={bookingDate:bookingDate};       
    return this.http.post('/api/frontend/getTimeSlotsForDate',json).map(
        (res)=>{
            return res.json();
        }
    );
  }
  
}




