import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class ThanksService {

  constructor(private http: Http) { }
  pageValidationMethod()
  {
      return this.http.get('/api/frontend/thanksPageValidation').map(
        (res)=>{
          console.log(res.json());
          return res.json();
        });
  }
  feeedbackFormMethod(stars, review)
  {
    var jsonData={stars,review};
      return this.http.post('/api/frontend/feedbackForm',jsonData).map(
        (res)=>{
          console.log(res.json());
          return res.json();
        });
  }
}



