import { Injectable } from '@angular/core';
import { Router, CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot } from "@angular/router";
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';
import { LoginSessionService } from "./login-session.service";

@Injectable()
export class AuthGuard implements CanActivate
{
    constructor(private http : Http, private router : Router, private loginService : LoginSessionService) {}

    canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot)
    {
        console.log('routerstate');
        console.log(state.root);        
        console.log(state.url);

        return this.loginService.checkLogin().map((res) => {
            console.log('state.url');
            console.log(res.status);
                if((state.url == '/login' || state.url == '/register') && res.status == 1)
                {
                    console.log('yessss');
                    this.router.navigate(['/booking']);
                    return false;
                }
                else if(state.url == '/profileInfo')
                {
                    this.loginPageRedirection(res.status);
                    if(res.status == 1 && !localStorage.getItem('bookingDetails'))
                    {
                        // if bookingDetails does not exist then redirect to booking page.
                        
                        this.router.navigate(['/booking']);
                        return false;
                    }
                    return true;

                }
                else if(state.url == '/thanks')
                {
                    this.loginPageRedirection(res.status);
                    if(res.status == 1 && !localStorage.getItem('paymentId'))
                    {
                        this.router.navigate(['/booking']);
                        return false;
                    }
                    return true;
                }
                else if(state.url == '/profile')
                {
                    this.loginPageRedirection(res.status);
                    return true;
                }
                else
                {
                    if(res.status == 1)
                    {
                        this.router.navigate(['/booking']);
                        return false;
                    }
                    return true;
                }
            
        });
    }
    loginPageRedirection(status)
    {
        if(status == 0)
        {
            this.router.navigate(['/login']); 
            return false;
        }
    }
}


