import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class HomeServiceService {

  constructor(private http: Http) { }

  servicesListingMethod()
  {
    return this.http.get('/api/frontend/servicesHomePage').map((resServices)=>{
      return resServices.json();
    });
  }

  productsListingMethod()
  {
    return this.http.get('/api/frontend/productsHomePage').map((resProducts)=>{      
      return resProducts.json();
    });
  }
}



