import { Component, OnInit } from '@angular/core';
import { routerTransition } from '../../router.animations';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatSnackBar } from "@angular/material";

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css'],
  animations: [routerTransition()],
  host: { '[@routerTransition]': '' }
})
export class ContactComponent implements OnInit {

title: string = 'Jitu Barber Shop';
  // lat: number = 51.678418;
  // lng: number = 7.809007;
  lat: number = 19.2141681;
  lng: number = 72.8720717;
  


  myForm: FormGroup;

    
    constructor(fb: FormBuilder, private matSnackBar: MatSnackBar) {
        this.myForm = fb.group({
            name: [null, Validators.compose([Validators.required])],
            email: [null, Validators.compose([Validators.required])],
            subject: [null, Validators.compose([Validators.required])],
            body: [null, Validators.compose([Validators.required])]        
        });
    }

  ngOnInit() {
  }

  onSubmit(formValue, formValid)
  {
    
    if(formValid == false)
    {
      this.matSnackBar.open('Please check fields.','Ok',{duration:3000});
      return;
    }
    else
    {
      this.matSnackBar.open('Your information sent successfully.','Ok',{duration:3000});
    }
  }
}



