import { Component, OnInit } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { FormBuilder, FormGroup, Validators, AbstractControl } from '@angular/forms';

@Component({
  selector: 'app-user-reviews-popup',
  templateUrl: './user-reviews-popup.component.html',
  styleUrls: ['./user-reviews-popup.component.css']
})
export class UserReviewsPopupComponent implements OnInit {
  stars: AbstractControl;
  review: AbstractControl;
  myForm: FormGroup;
  errorMessage: Boolean=false;
  constructor(fb: FormBuilder, public dialogRef: MatDialogRef<UserReviewsPopupComponent>,) {
    this.myForm=fb.group({
      stars:[null],
      review: [null]
    });
    // quantity:[null,Validators.compose([Validators.required, Validators.pattern('^\d+$')])]
    this.stars=this.myForm.controls['stars'];
    this.review=this.myForm.controls['review'];    
   }

  ngOnInit() {
  }

  onSubmit(formValue, formValid)
  {
    if(formValid == false)
    {
      this.errorMessage=true;
      return;
    }
    console.log('welcome after submit');
    console.log(formValue.stars);
    this.dialogRef.close(); 

  }
}



