import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class EditProfileServiceService {

  constructor(private http: Http) { }
  editProfileMethod()
  {
    return this.http.get('/api/frontend/getProfileInfo').map(
      (res)=>{
        return res.json();
      }
    )
  }

  updateProfileMethod(updatedValues)
  {
    return this.http.post('/api/frontend/updateProfile',updatedValues).map((res)=>{
      return res.json();
    });
  } 
}



