// modules
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule } from '@angular/router';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { CustomMaterializeModule } from './CustomMaterializeModule.module';
import {AgmCoreModule} from '@agm/core'
import { Angular2FontawesomeModule } from 'angular2-fontawesome/angular2-fontawesome'
import { HttpClientModule } from "@angular/common/http";

// components
import { AppComponent } from './app.component';
import { HeaderComponent } from './header/header.component';
import { HomeComponent } from './navigationPages/home/home.component';
import { AboutComponent } from './navigationPages/about/about.component';
import { ContactComponent } from './navigationPages/contact/contact.component';
import { NoPageExistComponent } from './navigationPages/no-page-exist/no-page-exist.component';
import { navigationRoutes } from './navigationRoutes.routes';
import { BookingServiceComponent } from './navigationPages/booking-service/booking-service.component';
import { ProfileInfoComponent } from './navigationPages/profile-info/profile-info.component';
import { LoginComponent } from './navigationPages/login/login.component';
import { RegisterComponent } from './navigationPages/register/register.component';
import { FooterComponent } from './footer/footer.component';
import { ThanksComponent } from './navigationPages/thanks/thanks.component';
import { BookingComponent } from './navigationPages/booking/booking.component';
import { BookingProductComponent } from './navigationPages/booking-product/booking-product.component';
import { PaymentWithoutBookingComponent } from './navigationPages/payment-without-booking/payment-without-booking.component';

import * as _ from "lodash";

// service
import { LoginSessionService } from './shared/login-session.service';
import { CanActivateViaAuthGuard } from './header/service/routeGuard';
import { AppointmentBookingService } from './navigationPages/appointment-booking/services/appointment-booking-service.service';

// product popup
import { ProductPopupComponent } from './navigationPages/product-popup/product-popup.component';
import { SingleProductComponent } from './navigationPages/single-product/single-product.component';

import { UserReviewsPopupComponent } from './navigationPages/user-reviews-popup/user-reviews-popup.component';
import { EditProfileComponent } from './navigationPages/edit-profile/edit-profile.component';
import { AppointmentBookingComponent } from './navigationPages/appointment-booking/appointment-booking.component';

// import { EditProfileServiceService } from './edit-profile-service.service';
import { AuthGuard } from "./shared/authGuard.service";

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    HomeComponent,
    AboutComponent,
    ContactComponent,
    NoPageExistComponent,
    BookingServiceComponent,
    ProfileInfoComponent,
    LoginComponent,
    RegisterComponent,
    ThanksComponent,
    BookingComponent,
    BookingProductComponent,
    ProductPopupComponent,
    PaymentWithoutBookingComponent,
    FooterComponent,
    SingleProductComponent,    
    UserReviewsPopupComponent, 
    EditProfileComponent, AppointmentBookingComponent
    
    
    
  ],
  imports: [
    BrowserModule,
    FormsModule,
      HttpModule,
      BrowserAnimationsModule,
      ReactiveFormsModule,
      RouterModule.forRoot(navigationRoutes), 
      CustomMaterializeModule,
       AgmCoreModule.forRoot({
      apiKey: 'AIzaSyCEz-UTADDP_dsMkYwRkk-T6r-Z21FoewI'
    }),
    Angular2FontawesomeModule,
    HttpClientModule
    
      
  ],
  entryComponents: [
    ProductPopupComponent,
    UserReviewsPopupComponent
  ],
  providers: [LoginSessionService, CanActivateViaAuthGuard, AppointmentBookingService, AuthGuard],
  bootstrap: [AppComponent]
})
export class AppModule { }



