import { Injectable } from '@angular/core';
import { CanActivate, Router } from '@angular/router';
import { LoginSessionService } from '../../shared/login-session.service';

@Injectable()
export class CanActivateViaAuthGuard implements CanActivate {

  constructor(private loginSessionService: LoginSessionService, private router: Router) {}

  canActivate() {
      var returnVal = false;
        this.loginSessionService.checkLogin().subscribe((loginResponse) => {
        if (loginResponse.loginSessionValue) {
            console.log('true login');
            returnVal = true;                
        }
        else
        {
            console.log('false login');
            this.router.navigate(['/unauthorized']);
            returnVal = false;
        }
    });
    return returnVal;
  }
}


