import { HomeComponent } from './navigationPages/home/home.component';
import { AboutComponent } from './navigationPages/about/about.component';
import { ContactComponent } from './navigationPages/contact/contact.component';
import { NoPageExistComponent } from './navigationPages/no-page-exist/no-page-exist.component';
import { BookingServiceComponent } from './navigationPages/booking-service/booking-service.component';
import { ProfileInfoComponent } from './navigationPages/profile-info/profile-info.component'; 
import { LoginComponent } from './navigationPages/login/login.component'; 
import { RegisterComponent } from './navigationPages/register/register.component';
import { ThanksComponent } from './navigationPages/thanks/thanks.component'; 
import { BookingComponent } from './navigationPages/booking/booking.component';
import { BookingProductComponent } from './navigationPages/booking-product/booking-product.component';
import { AppointmentBookingComponent } from './navigationPages/appointment-booking/appointment-booking.component';

import { ProductPopupComponent  } from './navigationPages/product-popup/product-popup.component';
import { PaymentWithoutBookingComponent  } from './navigationPages/payment-without-booking/payment-without-booking.component';
import { SingleProductComponent } from './navigationPages/single-product/single-product.component';
import { EditProfileComponent } from './navigationPages/edit-profile/edit-profile.component';
import { CanActivateViaAuthGuard } from './header/service/routeGuard';
import { AuthGuard } from "./shared/authGuard.service";

// services
import { AppointmentBookingService } from './navigationPages/appointment-booking/services/appointment-booking-service.service';

import { Routes } from '@angular/router';

export const navigationRoutes: Routes = [
    { path: 'home', component: HomeComponent },
    { path: 'about', component: AboutComponent },
    { path: 'contact', component: ContactComponent },
    { path: 'booking-service', component: BookingServiceComponent },
    { path: 'profileInfo', component: ProfileInfoComponent , canActivate : [AuthGuard]},
    { path: 'login', component: LoginComponent, canActivate : [AuthGuard] },
    { path: 'register', component: RegisterComponent , canActivate : [AuthGuard]},
    { path: 'thanks', component: ThanksComponent, canActivate : [AuthGuard] },
    { path: 'booking', component: BookingComponent},
    { path: 'booking-product', component: BookingProductComponent},
    { path: 'booking-appointment', component: AppointmentBookingComponent, resolve:{appointmentData:AppointmentBookingService}},
    { path: 'payment-without-booking', component: PaymentWithoutBookingComponent},
    { path: 'single-product/:id', component: SingleProductComponent},
    { path: 'profile', component: EditProfileComponent, canActivate : [AuthGuard]},
    { path: '', component: HomeComponent}, 
    { path: 'unauthorized', component: HomeComponent },
    { path: ':any', component: NoPageExistComponent },
    { path: '**', component: NoPageExistComponent },
]; 

/* { path: 'profile', component: EditProfileComponent,  canActivate: [
    CanActivateViaAuthGuard
  ]} */


