import { MatInputModule, MatButtonModule, MatSelectModule, MatDatepickerModule,
    MatNativeDateModule, MatDialogModule, MatSidenavModule, MatCardModule, MatProgressBarModule,
    MatTableModule, MatSnackBarModule, MatProgressSpinnerModule } from '@angular/material';
import {CdkTableModule} from '@angular/cdk/table';

@NgModule({
    imports: [MatInputModule, MatButtonModule, MatSelectModule, MatDatepickerModule,
     MatNativeDateModule, MatDialogModule, MatSidenavModule, MatCardModule, MatProgressBarModule,
     MatTableModule, CdkTableModule, MatSnackBarModule, MatProgressSpinnerModule],
    exports: [MatInputModule, MatButtonModule, MatSelectModule, MatDatepickerModule,
     MatNativeDateModule, MatDialogModule, MatSidenavModule, MatCardModule, MatProgressBarModule,
     MatTableModule, CdkTableModule, MatSnackBarModule, MatProgressSpinnerModule]
    
})

export class CustomMaterializeModule {


 }


