import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { DbDataService } from '../../shared/db-data.service';
import { LoginSessionService } from '../../shared/login-session.service';
import { Router } from '@angular/router';
import { ProfileInfoService  } from './services/profile-info.service';
import { LoginDetailsModel } from './models/loginDetailsModel.model';
import { BookingDetailsModel } from './models/bookingDetailsModel.model';
import { routerTransition } from '../../router.animations';
import { environment } from "../../../environments/environment";
import { WindowObject } from './windowObject';

@Component({
  selector: 'app-profile-info',
  templateUrl: './profile-info.component.html',
  styleUrls: ['./profile-info.component.css'],  
  providers: [DbDataService, ProfileInfoService, WindowObject],
  animations: [routerTransition()],
  host: { '[@routerTransition]': '' }
})
// inputs: ['genderValuePass', 'hairStyleDropdownValuePass', 'dateValuePass'],
export class ProfileInfoComponent implements OnInit {
    /* testValue3: string;
    profileInfoVal: string = 'hiii'; */
    myForm: FormGroup;
    /*  state: AbstractControl;
    city: AbstractControl;
    phoneNumber: AbstractControl; */

    paymentInfo;
    loginDetails: LoginDetailsModel;
    bookingDetails: BookingDetailsModel[];

    firstName: string;
    lastName: string;
    email: string;

    state: any;
    city: any;
    phoneNumber: any;

    bookingInfo: any;
    constructor(fb: FormBuilder, private dbDataService: DbDataService, private loginSessionService: LoginSessionService, private router: Router, private profileInfoService: ProfileInfoService, private windowObject: WindowObject) {

        // when page loads it checks weather user login or not
        //  loginSessionService.checkLogin().subscribe(
        //     (res) => {
                
        //         /// console.log(res + 'sa' + localStorage.getItem('bookingToken'));
        //         if (res.loginSessionValue != true || !localStorage.getItem('bookingToken')) {
        //             router.navigateByUrl('booking');
        //         }
        //         else {
        //             if (res.loginSessionValue == true && !localStorage.getItem('bookingToken')) {
        //                 router.navigateByUrl('booking');
        //             }
        //         }
        //     }
        // ); 
        
        this.bookingInfo=JSON.parse(localStorage.getItem('bookingDetails'));
        // bookingType = 1 for service, bookingType = 2 for product, bookingType = 3 for offline payment, bookingType = 4 for appointment booking
        let id: any;
        if(this.bookingInfo.bookingType == 1)
        {
            id=this.bookingInfo.serviceType; // service id
        }
        else if(this.bookingInfo.bookingType == 2)
        {
            id=this.bookingInfo.id; // product id
            console.log(id+' id');
        }  
        else if(this.bookingInfo.bookingType == 4)
        {
            id=this.bookingInfo.id; // appointment id
            console.log(id+' id');
        }
        else
        {
            if(this.bookingInfo.bookingType == 3)
            {
                id=null; // product id
                
            }   
        }

        profileInfoService.getLoginInfoAndBookingInfoMethod(id, this.bookingInfo.bookingType).subscribe(
                (res)=>{          
                    console.log('JSON.stringify(res)');
                    // console.log(JSON.stringify(res));
                    this.loginDetails=res.userData;
                    this.bookingDetails=res.serviceOrProductData;
                    this.email=this.loginDetails.email;
                    this.firstName=this.loginDetails.firstName;
                    this.lastName=this.loginDetails.lastName;                    
                    this.myForm.patchValue({state:this.loginDetails.state, city: this.loginDetails.city, phoneNumber: this.loginDetails.phoneNumber});                    
                    this.city=this.loginDetails.city;
                    this.phoneNumber=this.loginDetails.phoneNumber;
                    
                }); 


        this.myForm = fb.group({
            state: [null, Validators.compose([Validators.required])],
            city: [null, Validators.compose([Validators.required])],
            phoneNumber: [null, Validators.compose([Validators.required])]
        });

        

}
    onSubmit(formValue, formValid) {
        if(formValid == false)
        {
          console.log('form validaton error');          
          return;
        }
        console.log('payment enter');
        console.log(this.bookingInfo);
        console.log(this.bookingDetails);
        
        // console.log(JSON.parse(localStorage.getItem('bookingDetails')));
        // console.log(JSON.parse());
        /* this.dbDataService.payment().subscribe(
            paymentInfo => {
                this.paymentInfo = paymentInfo;
                // console.log(JSON.stringify(this.paymentInfo));
                console.log(this.paymentInfo);
            }
        ); */
    let price;  
    let description: any; 
    if(this.bookingInfo.bookingType == 1)
    {
        price=parseInt(this.bookingInfo.price)*100;
        description=this.bookingDetails[0].serviceTypeName; 
    } 
    else if(this.bookingInfo.bookingType == 2)
    {        
        price=parseInt(this.bookingInfo.price) * parseInt(this.bookingInfo.quantity) * 100;    
        description=this.bookingDetails[0].name;
        // let price1=parseInt(this.bookingInfo.quantity);
        // console.log(price +'price' + price1 + 'price1');    
    }
    else if(this.bookingInfo.bookingType == 4)
    {
        price = parseInt(this.bookingDetails[0].price) * 100;
        description = this.bookingInfo.companyName;
    }
    else
    {
        if(this.bookingInfo.bookingType == 3)
        {
            price=parseInt(this.bookingInfo.serviceOrProductPrice)*100;
            description=this.bookingInfo.serviceOrProductName;
        }
    }
        
    
    let  options = {
        "key": "rzp_test_xTVYVlOmAgyUyO",
        "amount": price, // 2000 paise = INR 20
        "name": "Jitu Barber",
        "description": "hiii",
        "image": "/your_logo.png",
        "handler": function (response){
           alert(response.razorpay_payment_id);
           alert('hello g');
        },
        "prefill": {
            "name": this.firstName+' '+this.lastName,
            "email": this.email
        },
        "notes": {
            "name": this.firstName+' '+this.lastName,
            "email": this.email
        },
        "theme": {
            "color": "#F37254"
        }
    }; 
    
    
    options.handler = ((response) => {

        let paymentId = response.razorpay_payment_id;
        console.log('paymentId');
        console.log(paymentId);
        if(this.bookingInfo.bookingType == 1)
        {
            this.profileInfoService.payment(paymentId, this.bookingInfo.serviceType, this.bookingInfo.timeSlot, formValue.phoneNumber, formValue.state, formValue.city, this.bookingInfo.bookingDate, this.bookingInfo.bookingType, price).subscribe((paymentResponse)=>{         
                console.log('within api 1');      
                localStorage.removeItem('bookingDetails'); 
                localStorage.setItem('paymentId', paymentId);
                // this.router.navigateByUrl('thanks');
                window.location.href = `${environment.appBaseUrl}thanks`;
                
            });
        }
        else if(this.bookingInfo.bookingType == 2)
        {
            this.profileInfoService.payment(paymentId, this.bookingInfo.id , this.bookingInfo.quantity, formValue.phoneNumber, formValue.state, formValue.city, null, this.bookingInfo.bookingType, price).subscribe((paymentResponse)=>{
            localStorage.removeItem('bookingDetails');
            localStorage.setItem('paymentId', paymentId);
            // this.router.navigateByUrl('thanks');
            window.location.href = `${environment.appBaseUrl}thanks`;
            
            });            
        }
        else if(this.bookingInfo.bookingType == 4)
        {
            console.log('within api4');       
            this.profileInfoService.payment(paymentId, this.bookingInfo.id , this.bookingInfo.timeSlot, formValue.phoneNumber, formValue.state, formValue.city, this.bookingInfo.bookingDate, this.bookingInfo.bookingType, price,this.bookingInfo.companyName, this.bookingInfo.description ).subscribe((paymentResponse)=>{
                console.log('paymentResponse');
                console.log(paymentResponse);
                localStorage.removeItem('bookingDetails');
                localStorage.setItem('paymentId', paymentId);
                // this.router.navigateByUrl('thanks');
                window.location.href = `${environment.appBaseUrl}thanks`;
                
            });
        }
        else
        {
            if(this.bookingInfo.bookingType == 3)
            {
                this.profileInfoService.payment(paymentId, this.bookingInfo.serviceOrProductPrice, this.bookingInfo.serviceOrProductName, formValue.phoneNumber, formValue.state, formValue.city, null, this.bookingInfo.bookingType, null).subscribe((paymentResponse)=>{
                    // this.router.navigateByUrl('thanks');
                    localStorage.removeItem('bookingDetails');
                    localStorage.setItem('paymentId', paymentId);
                    window.location.href = `${environment.appBaseUrl}thanks`;
                });
            }
        }
     });
    
     let rzp1 = this.windowObject.nativeWindow.Razorpay(options);
     rzp1.open();
    

    }



  // save data in payment table.
  


  ngOnInit() {
  }

}



