import { Component, OnInit } from '@angular/core';
import { Inject, Injectable } from '@angular/core';
//import { LoginSessionService } from '../../shared/login-session.service';
import {PaymentDataServicesModel} from '../models/paymentDataServicesModel.model';
import {PaymentDataProductsModel} from '../models/paymentDataProductsModel.model';
import {PaymentDataWithoutBookingModel} from '../models/paymentDataWithoutBookingModel.model';
import { PaymentDataAppointmentBookingModel } from "../models/paymentDataAppointmentBooking.model";
import { BookingService } from './booking.service';

import {DataSource} from '@angular/cdk/table';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {Observable} from 'rxjs/Observable';

@Injectable()
export class ServiceDatabase {
  dataChange: BehaviorSubject<PaymentDataServicesModel[]> = new BehaviorSubject<PaymentDataServicesModel[]>([]);
  get data(): PaymentDataServicesModel[] { return this.dataChange.value; }  
  constructor(private bookingService: BookingService) {
      this.bookingService.getBookedServicesDataMethod().subscribe((servicesData) => {
      console.log('services data -------- :');
      console.log(servicesData);
      this.dataChange.next(
        servicesData.paymentDataServices
      );   
    });
    
  }
}

@Injectable()
export class ProductDatabase {
  dataChange: BehaviorSubject<PaymentDataProductsModel[]> = new BehaviorSubject<PaymentDataProductsModel[]>([]);
  get data(): PaymentDataProductsModel[] { return this.dataChange.value; }
  constructor(private bookingService: BookingService) {
      this.bookingService.getBookedProductsDataMethod().subscribe((productsData) => {
          console.log(productsData);
          this.dataChange.next(productsData.paymentDataProducts);
      });
  }
}


@Injectable()
export class NoBookDatabase {
  dataChange: BehaviorSubject<PaymentDataWithoutBookingModel[]> = new BehaviorSubject<PaymentDataWithoutBookingModel[]>([]);
  get data(): PaymentDataWithoutBookingModel[] { return this.dataChange.value; }
  constructor(private bookingService: BookingService) {
    this.bookingService.getWithoutBookingDataMethod().subscribe((withoutBookingData) => {
          console.log(withoutBookingData);
           this.dataChange.next(withoutBookingData.paymentDataWithoutBooking);
    });
  }
}

@Injectable()
export class AppointmentDatabase {
  dataChange : BehaviorSubject<PaymentDataAppointmentBookingModel[]> = new BehaviorSubject<PaymentDataAppointmentBookingModel[]>([]);
  get data() : PaymentDataAppointmentBookingModel[] { return this.dataChange.value; }
  constructor(private bookingService: BookingService) {
    this.bookingService.getBookedAppointmentsDataMethod().subscribe((appointmentBookingData) => {
      console.log(appointmentBookingData);
      this.dataChange.next(appointmentBookingData);
    });
  }
}



