import { Component, OnInit } from '@angular/core';
import { ProductPopupComponent  } from '../product-popup/product-popup.component';
import {MatDialog, MatDialogRef} from '@angular/material';
import { SingleProductServiceService } from './services/single-product-service.service';
import { ProductDetailsModel } from './models/productDetailsModel.model';
import { Router } from '@angular/router';

@Component({
  selector: 'app-single-product',
  templateUrl: './single-product.component.html',
  styleUrls: ['./single-product.component.css'],
  providers: [SingleProductServiceService]
})
export class SingleProductComponent implements OnInit {
selectedOption: string;
productDetails: ProductDetailsModel;
  constructor(public dialog: MatDialog, private singleProductService: SingleProductServiceService, private router: Router) {
    let urlParam=window.location.href;
    let urlParamArr=urlParam.split('/');
    let productId=urlParamArr[urlParamArr.length - 1];
    

    singleProductService.getSingleProductDetail(productId).subscribe((res)=>{
      console.log(res.productDetails);
      if(res.productDetails != '')
      {
        this.productDetails=res.productDetails;        
      }
      else
      {
        this.router.navigateByUrl('booking-product');
      }

    });

   }

  ngOnInit() {
  }
  onSubmit(id, price, name)
  {
    let dialogRef = this.dialog.open(ProductPopupComponent, {
      data:{id,price,name}      
    }
    );
    dialogRef.afterClosed().subscribe(result => {            
      this.selectedOption = result;
    });  
       
  }
}



